<template>
  <a-modal
    title="修改名称"
    :visible="updateName"
    width="30%"
    @ok="handleOk"
    @cancel="handleCancel"
    destroyOnClose
  >
    <div class="body">
      <div>
        <span style="font-weight: 600">文件夹名称:</span>
        <a-input
          style="width: 15rem; margin-left: 2rem"
          placeholder="请输入文件夹名称"
          v-model="form.name"
        />
      </div>
    </div>
  </a-modal>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "updateName",
  components: {},
  props: {
    fileName: String,
    sign: String,
  },
  data() {
    return {
      form: {
        name: "",
      },
    };
  },
  computed: {
    ...mapState({
      updateName: (state) => state.folder.updateName,
      id: (state) => state.folder.id,
    }),
  },
  watch: {},
  methods: {
    handleOk() {
      if (this.sign == "1") {
        this.$ProjectModels
          .updateFolderName(this.id, null, this.form)
          .then((res) => {
            this.$ProjectModels.allLibrary().then((res) => {
              this.$emit("updateLibrary", res.data);
            });
          })
          .then((res) => {
            this.form.name = "";
            this.$store.commit("changeupdateName", false);
          });
      } else if (this.sign == "2") {
        this.$ProjectModels
          .updateFileName(this.id, this.fileName, null, this.form)
          .then((res) => {
            this.$ProjectModels.getFolder(this.id).then((res) => {
              this.$emit("updateLibrary", res.data);
            });
          })
          .then((res) => {
            this.form.name = "";
            this.$store.commit("changeupdateName", false);
          });
      }
    },
    handleCancel() {
      this.form.name = "";
      this.$store.commit("changeupdateName", false);
    },
  },
  created() {},
  mounted() {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  activated() {},
};
</script>
<style lang="less" scoped>
.body {
  width: 100%;
}
</style>