<template>
  <a-modal
    v-model="tablemodel"
    title="table"
    width="70%"
    destroyOnClose
    @ok="handleOk"
    @cancel="handleCancel"
  >
    <a-spin tip="Loading..." :spinning="loadding" size="large">
      <div class="tableList" @scroll="handeltable" ref="tablelist">
        <div class="table">
          <div
            v-for="(value, key, index) in tablenums.result"
            :key="index"
            class="cloumns"
          >
            <div class="title">{{ key }}</div>
            <div
              class="content"
              v-for="(value, key, index) in tablenums.result[key]"
              :key="index"
            >
              <div class="text">
                {{ value }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </a-spin>
  </a-modal>
</template>

<script>
import { mapState } from "vuex";
export default {
  components: {},
  props: {
    tablenum: Object,
    fileName: String
  },
  data() {
    return {
      scrollLeft: "",
      tablenums: {},
      params: {
        start: 0,
        end: 100
      },
      flag: 1
    };
  },
  computed: {
    ...mapState({
      tablemodel: state => state.tableModel.tablemodel,
      loadding: state => state.folder.loadding,
      id: state => state.folder.id
    })
  },
  watch: {
    tablenum: {
      handler(n, o) {
        this.tablenums = n;
      },
      deep: true
    }
  },
  methods: {
    handleOk() {
      this.$store.commit("gettablemodel", false);
    },
    handleCancel() {
      this.$store.commit("gettablemodel", false);
    },
    handeltable(e) {
      //向下滚动
      let scrollBottom =
        e.target.scrollHeight - e.target.scrollTop - e.target.clientHeight;
      if (scrollBottom <= 0 && this.params.end <= this.tablenum.count) {
        this.flag = 0;
        this.params.start = this.params.end;
        this.params.end = Number(this.params.end) + 100;
        this.$ProjectModels
          .getFolderContent(this.id, this.fileName, this.params)
          .then(res => {
            if (typeof res.data === "string") {
              this.tablenums = eval("(" + res.data + ")");
            } else {
              this.tablenums = res.data;
            }
          })
          .then(res => {
            this.$store.commit("changeloadding", false);
            this.$refs.tablelist.scrollTop = 10;
          });
      }
      //向上滚动
      if (this.$refs.tablelist.scrollTop == 0 && this.params.start != 0) {
        this.params.end = this.params.start;
        this.params.start = this.params.start - 100;
        this.$ProjectModels
          .getFolderContent(this.id, this.fileName, this.params)
          .then(res => {
            if (typeof res.data === "string") {
              this.tablenums = eval("(" + res.data + ")");
            } else {
              this.tablenums = res.data;
            }
          })
          .then(res => {
            this.$store.commit("changeloadding", false);
            this.$refs.tablelist.scrollTop = 10;
          });
      } else if (
        this.$refs.tablelist.scrollTop == 0 &&
        this.params.start == 0 &&
        this.flag == 0
      ) {
        this.flag = 1;
        this.$message.warning("到最顶部了");
        console.log("到顶部了");
      }
    }
  },
  created() {},
  mounted() {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  activated() {}
};
</script>
