      border-left: 0.1rem solid #ccc;
      font-weight: 700;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      line-height: 3rem;
    }
    .content {
      width: 100%;
      height: 3rem;
      background-color: rgb(248, 240, 229);
      border-bottom: 0.1rem solid #ccc;
      border-left: 0.1rem solid #ccc;
      line-height: 3rem;
      .text {
        width: 10rem;
        font-weight: 600;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
}
</style>