<template>
  <div class="folder">
    <div class="header"></div>
    <a-spin tip="Loading..." :spinning="loadding" size="large">
      <a-table
        :columns="columns"
        :dataSource="librarys"
        :row-key="(r, i) => i.toString()"
        size="middle"
        :row-selection="{
          selectedRowKeys: selectedRowKeys,
          onChange: onSelectChange,
        }"
      >
        <template slot="title" style="background-color: #dfe9f5">
          <a-button @click="back"><a-icon type="arrow-left" /></a-button>
          <a-button
            style="margin-left: 0.5rem"
            @click="uploadFolder"
            v-if="state == 0"
            ><a-icon type="upload"
          /></a-button>
          <a-button
            v-if="state == 1"
            @click="uploadFile"
            style="margin-left: 0.5rem"
          >
            <a-icon type="upload" />
          </a-button>
          <a-popconfirm
            :title="title"
            ok-text="Yes"
            cancel-text="No"
            @confirm="delMultiple"
          >
            <a-button style="margin-left: 0.5rem"
              ><a-icon type="delete"
            /></a-button>
          </a-popconfirm>
        </template>
        <div slot="name" slot-scope="text, record">
          <span @click="showFolder(record)" v-if="record.hasOwnProperty('id')"
            ><a>{{ record.name }}</a></span
          >
          <span v-if="record.hasOwnProperty('size')"
            ><a>{{ record.name }}</a></span
          >
        </div>
        <div
          slot="size"
          slot-scope="text, record"
          v-if="record.hasOwnProperty('size')"
        >
          <span>{{ unitConversion(record.size) }}</span>
        </div>
        <div slot="id" slot-scope="text, record">
          <div
            style="width: 1.5rem; height: 1.5rem"
            v-if="record.hasOwnProperty('id')"
          >
            <img
              style="width: 100%; height: 100%"
              src="../../../assets/icon/文件夹.png"
            />
          </div>
          <div
            style="width: 1.2rem; height: 1.2rem"
            v-if="record.hasOwnProperty('size')"
          >
            <img
              style="width: 100%; height: 100%"
              :src="fileType(record.name.split('.').pop().toLowerCase())"
            />
          </div>
        </div>
        <div
          slot="filterDropdown"
          slot-scope="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          style="padding: 8px"
        >
          <a-input
            v-ant-ref="(c) => (searchInput = c)"
            :placeholder="`Search ${column.dataIndex}`"
            :value="selectedKeys[0]"
            style="width: 188px; margin-bottom: 8px; display: block"
            @change="
              (e) => setSelectedKeys(e.target.value ? [e.target.value] : [])
            "
            @pressEnter="
              () => handleSearch(selectedKeys, confirm, column.dataIndex)
            "
          />
          <a-button
            type="primary"
            icon="search"
            size="small"
            style="width: 90px; margin-right: 8px"
            @click="() => handleSearch(selectedKeys, confirm, column.dataIndex)"
          >
            Search
          </a-button>
          <a-button
            size="small"
            style="width: 90px"
            @click="() => handleReset(clearFilters)"
          >
            Reset
          </a-button>
        </div>
        <span slot="action" slot-scope="text, record">
          <a v-if="record.hasOwnProperty('id')" @click="updatename(record, 1)"
            >修改名称</a
          >
          <a v-if="record.hasOwnProperty('size')" @click="updatename(record, 2)"
            >修改名称</a
          >
          <a-divider type="vertical" />
          <a-popconfirm
            title="Are you sure？"
            ok-text="Yes"
            cancel-text="No"
            @confirm="del(record)"
          >
            <a style="color: red"> 删除</a>
          </a-popconfirm>
          <a-divider type="vertical" v-if="record.hasOwnProperty('size')" />
          <a @click="view(record)" v-if="record.hasOwnProperty('size')">
            查看</a
          >
          <!-- <a style="margin-left: 1rem" v-if="record.hasOwnProperty('size')">
          下载
        </a> -->
        </span>
      </a-table>
    </a-spin>
    <Table :tablenum="tablenum" :fileName="fileName" />
    <uploadFolder @allLibrary="allLibrary" />
    <updateName
      @updateLibrary="updateLibrary"
      :fileName="fileName"
      :sign="sign"
    />
    <updateFile @uploadlibrarys="uploadlibrarys" />
  </div>
</template>

<script>
import { fileType } from "@/utils/icon.js";
import { unitConversion } from "@/utils/utils.js";
import { mapState } from "vuex";
import Table from "./table.vue";
import uploadFolder from "./uploadFolder.vue";
import updateFile from "./uploadFile.vue";
import updateName from "./updateName.vue";
const columns = [
  {
    dataIndex: "id",
    key: "id",
    title: "",
    scopedSlots: { customRender: "id" },
  },
  {
    dataIndex: "name",
    key: "name",
    title: "名称",
    sorter: true,
    scopedSlots: {
      customRender: "name",
      filterDropdown: "filterDropdown",
      filterIcon: "filterIcon",
    },
  },
  {
    dataIndex: "update_time",
    key: "update_time",
    title: "更新时间",
    sorter: true,
  },
  {
    dataIndex: "size",
    key: "size",
    title: "文件大小",
    sorter: true,
    scopedSlots: { customRender: "size" },
  },
  {
    key: "action",
    title: "操作",
    scopedSlots: { customRender: "action" },
  },
];
function onChange(pagination, filters, sorter) {
  // var keys = Object.keys(filters)
  console.log("params", filters);
  for (var i in filters) {
    this.order.search[i] = filters[i][0];
  }
  this.order.ordering[sorter.field] = sorter.order;
}
export default {
  name: "Folder",
  components: {
    Table,
    uploadFolder,
    updateFile,
    updateName,
  },
  props: {
    Librarys: Array,
  },
  data() {
    return {
      searchText: "",
      searchInput: null,
      searchedColumn: "",
      columns,
      librarys: [],
      selectedRowKeys: [],
      title: "",
      selections: [],
      tablenum: {}, //存储表格里的数据
      state: 0,
      fileName: "", //文件名称

      sign: "", //标志
      params: {
        start: 0,
        end: 10,
      },
    };
  },
  computed: {
    ...mapState({
      tablemodel: (state) => state.tableModel.tablemodel,
      id: (state) => state.folder.id,
      name: (state) => state.folder.name,
      loadding: (state) => state.folder.loadding,
      navWorkflow: (state) => state.titleWorkflow.navWorkflow,
    }),
  },
  watch: {
    Librarys: {
      handler(n, o) {
        this.librarys = n;
      },
      deep: true,
    },
  },
  methods: {
    unitConversion,
    handleSearch(selectedKeys, confirm, dataIndex) {
      confirm();
      this.searchText = selectedKeys[0];
      this.searchedColumn = dataIndex;
    },
    handleReset(clearFilters) {
      clearFilters();
      this.searchText = "";
    },
    onChange,
    fileType,
    getAllLibrary() {
      this.$ProjectModels.allLibrary().then((res) => {
        this.librarys = res.data;
      });
    },
    onSelectChange(selectedRowKeys, selections) {
      this.selectedRowKeys = selectedRowKeys;
      this.selections = selections;
      this.title = "你确定要删除选择的" + this.selections.length + "项";
    },
    showFolder(e) {
      this.state = 1;
      this.$store.commit("getfolderId", e.id);
      this.$ProjectModels
        .getFolder(e.id)
        .then((res) => {
          this.librarys = res.data;
        })
        .then((res) => {
          let arr = [];
          arr.push(this.navWorkflow[0]);
          let obj_0 = {
            title: "library",
            sign: 2,
          };
          arr.push(obj_0);
          let obj_1 = {
            title: e.name,
            sign: 3,
          };
          arr.push(obj_1);
          this.$store.commit("postNavWorkflowData", arr);
        });
    },
    view(e) {
      this.fileName = e.name;
      this.$store.commit("gettablemodel", true);
      this.$store.commit("changeloadding", true);
      this.$ProjectModels
        .getFolderContent(this.id, e.name, this.params)
        .then((res) => {
          if (typeof res.data === "string") {
            this.tablenum = eval("(" + res.data + ")");
            console.log(this.tablenum);
          } else {
            this.tablenum = res.data;
          }
        })
        .then((res) => {
          this.$store.commit("changeloadding", false);
        });
    },
    //回退
    back() {
      let arr = [];
      arr.push(this.navWorkflow[0]);
      let obj = {
        title: "library",
        sign: 2,
      };
      arr.push(obj);
      this.$store.commit("postNavWorkflowData", arr);
      this.state = 0;
      this.$ProjectModels.allLibrary().then((res) => {
        this.librarys = res.data;
      });
    },
    //新建文件夹
    uploadFolder() {
      this.$store.commit("changeUploadFolder", true);
    },
    //allLibrary
    allLibrary(e) {
      this.librarys = e;
    },
    //打开上传文件的model
    uploadFile() {
      this.$store.commit("changeUploadFiles", true);
    },
    //上传文件后刷新表格
    uploadlibrarys(e) {
      this.librarys = e;
    },
    //修改名称后刷新表格
    updateLibrary(e) {
      this.librarys = e;
    },
    //修改文件夹名称
    updatename(e, index) {
      this.sign = index;
      if (index === 1) {
        this.$store.commit("getfolderId", e.id);
        this.$store.commit("changeupdateName", true);
      } else if (index === 2) {
        this.fileName = e.name;
        this.$store.commit("changeupdateName", true);
      }
    },
    //删除
    del(e) {
      if (this.state == 0) {
        this.$ProjectModels
          .deleteFolder(e.id)
          .then((res) => {
            this.$ProjectModels.allLibrary().then((res) => {
              this.librarys = res.data;
            });
          })
          .then((res) => {
            this.$message.success("删除成功");
          });
      } else if (this.state == 1) {
        this.$ProjectModels
          .deleteFile(this.id, e.name)
          .then((res) => {
            this.$ProjectModels.getFolder(this.id).then((res) => {
              this.librarys = res.data;
            });
          })
          .then((res) => {
            this.$message.success("删除成功");
          });
      }
    },
    //一次删除多个
    delMultiple() {
      if (this.state == 0) {
        for (let i in this.selections) {
          this.$ProjectModels
            .deleteFolder(this.selections[i].id)
            .then((res) => {
              this.$ProjectModels.allLibrary().then((res) => {
                this.librarys = res.data;
              });
            });
        }
      } else if (this.state == 1) {
        for (let i in this.selections) {
          this.$ProjectModels
            .deleteFile(this.id, this.selections[i].name)
            .then((res) => {
              this.$ProjectModels.getFolder(this.id).then((res) => {
                this.librarys = res.data;
              });
            });
        }
      }
      this.selections = [];
      this.selectedRowKeys = [];
    },
  },
  created() {},
  mounted() {
    this.getAllLibrary();
  },
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  activated() {},
};
</script>
<style lang="less" scoped>
.folder {
  width: 100%;
  height: 100%;
  .header {
    padding: 0 11.875rem 0 11.875rem;
    background-image: linear-gradient(-225deg, #00c2b2 10%, #56b7e4 100%);
  }
}
