<template>
  <a-modal
    title="上传文件"
    :visible="uploadFiles"
    width="50%"
    @ok="handleOk(0)"
    @cancel="handleCancel"
  >
    <a-spin
      tip="Loading..."
      :spinning="loadding"
      size="large"
    >
      <div class="body">
        <div class="action">
          <input
            type="file"
            id="files"
            class="upload-btn"
            ref="files"
            multiple
            @change="handleFilesUpload"
          />
          <span
            type="text"
            class="file-input-trigger"
            @click="updateFile"
          >上传文件</span>
        </div>
        <div class="fileList">
          <div
            style="
              width: 4rem;
              height: 4rem;
              margin-left: 3rem;
              margin-top: 0.7rem;
              float: left;
            "
            v-for="(i, index) in showFileList"
            :key="index"
          >
            <div style="width: 100%; height: 100%">
              <a-icon
                type="minus-circle"
                class="pull"
                @click="del(index)"
              />
              <img
                :src="i.value"
                alt=""
                style="width: 100%; height: 100%"
              />
              <div class="name">{{ i.name }}</div>
            </div>
          </div>
        </div>
      </div>
    </a-spin>
  </a-modal>
</template>

<script>
import { mapState } from "vuex";
import { getfileImgs, fileType } from "@/utils/utils.js";
import SparkMD5 from "spark-md5";
let Size = 1024 * 200;
export default {
  components: {},
  data() {
    return {
      fileList: [],
      showFileList: [], //展示的列表
      item: {},
      librarys: [],
    };
  },
  computed: {
    ...mapState({
      uploadFiles: (state) => state.folder.uploadFiles,
      id: (state) => state.folder.id,
      loadding: (state) => state.folder.loadding,
    }),
  },
  watch: {},
  methods: {
    getMd5(_file) {
      const _this = this;
      var blobSlice =
        File.prototype.slice ||
        File.prototype.mozSlice ||
        File.prototype.webkitSlice,
        file = _file,
        chunkSize = 1024 * 200, // Read in chunks of 2MB
        chunks = Math.ceil(file.size / chunkSize),
        currentChunk = 0,
        spark = new SparkMD5.ArrayBuffer(),
        fileReader = new FileReader();

      fileReader.onload = function (e) {
        // console.log("read chunk nr", currentChunk + 1, "of", chunks);
        spark.append(e.target.result); // Append array buffer
        currentChunk++;

        if (currentChunk < chunks) {
          loadNext();
        } else {
          // console.log("finished loading");
          const md5 = spark.end();
          // console.info("computed hash", md5); // Compute hash
          _this.item.md5 = md5;
        }
      };

      fileReader.onerror = function () {
        console.warn("oops, something went wrong.");
      };

      function loadNext() {
        var start = currentChunk * chunkSize,
          end = start + chunkSize >= file.size ? file.size : start + chunkSize;

        fileReader.readAsArrayBuffer(blobSlice.call(file, start, end));
      }

      loadNext();
    },
    handleOk(index) {
      for (let i in this.fileList) {
        let file = this.fileList[i];
        this.ShardUpload(index, file);
      }
    },
    ShardUpload(index, files) {
      let start = index * Size;
      if (start > files.size) {
        this.$ProjectModels.combineFile(this.id, files.name).then((res) => {
          this.$ProjectModels
            .getFolder(this.id)
            .then((res) => {
              this.$emit("uploadlibrarys", res.data);
            })
            .then((res) => {
              this.$store.commit("changeUploadFiles", false);
              this.$message.success("上传成功");
              this.showFileList = [];
              this.fileList = [];
            });
        });
        return;
      }
      let blob = files.slice(start, start + Size);
      let blobName = `${files.name}.tmp.${index}`;
      let blobFile = new File([blob], blobName);
      let formData = new FormData();
      formData.append("file", blobFile);
      this.$ProjectModels.uploadFile(this.id, formData).then((res) => {
        this.ShardUpload(++index, files);
      });
    },
    handleCancel() {
      this.$store.commit("changeUploadFiles", false);
    },
    updateFile() {
      this.$refs.files.click();
    },
    //本地上传file
    handleFilesUpload() {
      let uploadedFiles = this.$refs.files.files;
      var reader = new FileReader();
      var that = this;
      let arr = fileType();
      if (
        arr.indexOf(uploadedFiles[0].type) > -1 ||
        uploadedFiles[0].type == ""
      ) {
        this.showFileList.push({
          name: uploadedFiles[0].name,
          value: getfileImgs(uploadedFiles[0].type),
        });
      } else {
        reader.readAsDataURL(uploadedFiles[0]);
        reader.onload = function () {
          that.showFileList.push({
            name: uploadedFiles[0].name,
            value: this.result,
          });
        };
      }
      for (let i = 0; i < uploadedFiles.length; i++) {
        this.fileList.push(uploadedFiles[i]);
      }
      console.log(this.fileList);
    },
    //移除本地的文件
    del(index) {
      this.showFileList.splice(index, 1);
      this.fileList.splice(index, 1);
    },
  },
  created() { },
  mounted() { },
  beforeCreate() { },
  beforeMount() { },
  beforeUpdate() { },
  updated() { },
  beforeDestroy() { },
  destroyed() { },
  activated() { },
};
</script>
<style lang="less" scoped>
.body {
  width: 100%;
  height: 30rem;
  .action {
    .upload-btn {
      display: none;
    }
    /*美化自定义按钮*/
    .file-input-trigger {
      padding: 2px 5px;
      margin-left: 1rem;
      color: #fff;
      background-color: #3888c7;
      border: 1px solid #3888c7;
      border-radius: 5px;
      outline: none;
      cursor: pointer;
    }
  }
  .fileList {
    overflow: auto;
    width: 100%;
    height: 28rem;
    margin-top: 1rem;
    .pull {
      position: relative;
      float: right;
    }
    .pull:hover {
      color: red;
    }
    .name {
      overflow: hidden;
      width: 4rem;
