<template>
  <!-- 结果展示组件，主要用来展示分析结果，以及download代码和push markdown文档笔记 -->
  <div class="myResult">
    <div class="header">
      <keep-alive>
        <analyNav :currentTab="currentTab">
          <template v-slot:navWorkflow>
            <template v-for="items in navWorkflow">
              <div
                class="workflow-line"
                :key="items.sign"
                @click="toWorkflow(items.sign)"
              >
                &nbsp;&nbsp;<span class="workflow-text">{{ items.title }}</span>
                <template v-if="items.sign != navWorkflow.length">
                  <span>&nbsp;></span>
                </template>
              </div>
            </template>
          </template>
        </analyNav>
      </keep-alive>
    </div>

    <div class="content">
      <!-- <div class="title"> -->
      <!-- 简易标题组件 -->
      <!-- <showHeader :workflowName="workflowName"></showHeader> -->
      <!-- </div> -->

      <!-- 主要的操作区域 -->
      <div class="main">
        <Folder :Librarys="Librarys" />
      </div>
    </div>

    <div class="footer">
      <analyFooter></analyFooter>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import analyNav from "../../PublicComponment/analyNav";
import analyFooter from "../../PublicComponment/analyFooter";
import Folder from "./components/folder.vue";
export default {
  name: "Library",
  components: {
    // eslint-disable-next-line vue/no-unused-components
    analyNav,
    // eslint-disable-next-line vue/no-unused-components
    analyFooter,
    Folder,
  },
  data() {
    return {
      currentTab: "library",
      workflowName: null,
      Librarys: [],
    };
  },
  computed: {
    ...mapState({
      navWorkflow: (state) => state.titleWorkflow.navWorkflow,
    }),
  },
  watch: {},
  methods: {
    workflowNameEvent(n) {
      this.workflowName = n;
    },
    toWorkflow(e) {
      if (e == 2) {
        let arr = [];
        arr.push(this.navWorkflow[0]);
        let obj = {
          title: "library",
          sign: 2,
        };
        arr.push(obj);
        this.$store.commit("postNavWorkflowData", arr);
      }
      this.$ProjectModels.allLibrary().then((res) => {
        this.Librarys = res.data;
      });
    },
  },
  created() {},
  mounted() {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  activated() {},
};
</script>

