<template>
  <a-modal
    title="新建文件夹"
    :visible="uploadFolder"
    @ok="handleOk"
    @cancel="handleCancel"
    width="30%"
  >
    <div class="body">
      <div>
        <span style="font-weight: 600">文件夹名称:</span>
        <a-input
          style="width: 15rem; margin-left: 2rem"
          placeholder="请输入文件夹名称"
          v-model="form.name"
        />
      </div>
    </div>
  </a-modal>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "uploadFolder",
  components: {},
  data() {
    return {
      form: {
        name: "",
      },
    };
  },
  computed: {
    ...mapState({
      uploadFolder: (state) => state.folder.uploadFolder,
    }),
  },
  watch: {},
  methods: {
    handleOk() {
      this.$ProjectModels
        .createFolder(this.form)
        .then((res) => {
          this.$ProjectModels.allLibrary().then((res) => {
            this.$emit("allLibrary", res.data);
          });
        })
        .then((res) => {
          this.$store.commit("changeUploadFolder", false);
        });
    },
    handleCancel() {
      this.$store.commit("changeUploadFolder", false);
    },
  },
  created() {},
  mounted() {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  activated() {},
};
</script>
<style lang="less" scoped>
.body {
  width: 100%;
}
</style>